*, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

img {
  width: 100%;
}

h1, h2, h3 {
  line-height: 1;
}

a {
  text-decoration: none;
}

.container {
  max-width: 60rem;
  margin: 0 auto;
}
@media (max-width: 59.9375em) {
  .container {
    padding: 0 1.25rem;
  }
}

:root {
  --white: #fff;
  --black: #000;
}